$prefix: '.wc-booster';

#{$prefix}-product-companion {
	background: #fff;
	display: flex;
	justify-content: center;
	align-items: center;
	 padding: 15px 10px !important;
	position: absolute !important;
	z-index: 999;

	.block-editor-block-list__layout {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 12px;

		.wp-block-wc-booster-wish-list-button {
			position: unset !important;

			.wc-booster-wishlist-button {
				i {
					font-size : 16px !important;
				}
			}
		}

		.wp-block-wc-booster-quick-view {
			position: unset !important;
		}
	}
	.product-companion-cart {
		top: 5px !important;
	}
	&.row {
		flex-direction: row;
		gap: 14px;
		align-items: flex-end;
		padding: 8px 18px !important;
	}
	
}
